/*




ex [](http://demo3.ec-cube.net/)

Markup:
.ec-inlineBtn 
.ec-inlineBtn--primary 
.ec-inlineBtn--action 
.ec-inlineBtn--cancel 

Styleguide 2.1.1
*/
.ec-inlineBtn{
  @include btn-default;
}
.ec-inlineBtn--primary{
  @include btn-primary
}
.ec-inlineBtn--action{
  @include btn-action
}
.ec-inlineBtn--cancel{
  @include btn-cancel
}

