.ec-orderMails{
  & &__item{
    padding-bottom: 10px;
    @include borderBottom();
  }
  & &__time{
    margin: 0;
  }
  & &__body{
    display: none;
  }
}




/*
 

  Project 

ex [](http://demo3.ec-cube.net/mypage)
(  )

Markup:
include /assets/tmpl/elements/16.2.historyDetail.pug
+ec-historyDetailMailHistory

Styleguide 16.2.6
*/
.ec-orderMail{
  padding-bottom: 10px;
  @include borderBottom();
  margin-bottom: 16px;
  & &__time{
    margin: 0;
  }
  & &__body{
    display: none;
  }
  & &__time {
    margin-bottom: 4px;
  }
  & &__link {
    a {
      color: #0092C4;
      text-decoration: none;
      cursor: pointer;
    }
    a:hover {
      color: #33A8D0;
    }
    margin-bottom: 4px;
  }
  & &__close{
    a {
      color: #0092C4;
      text-decoration: none;
      cursor: pointer;
    }
    a:hover {
      color: #33A8D0;
    }
  }
}
