@import "../mixins/media";
@import "../mixins/variables";
/*




sg-wrapper:
<div class="ec-role">
  <sg-wrapper-content/>
</div>

Styleguide 1.1
*/

/*




ex [](http://demo3.ec-cube.net/products/detail/27)

Markup:
.ec-headingTitle 

Styleguide 1.1.1
*/
.ec-headingTitle{
  margin: 0 0 8px;
  font-size: 32px;
  font-weight: normal;
  color: #525263;
}

/*




ex [](http://demo3.ec-cube.net/help/agreement)

Markup:
.ec-pageHeader
  h1 

Styleguide 1.1.2
*/
.ec-pageHeader h1{
  margin: 0 0 8px;
  border-bottom: 1px dotted #ccc;
  border-top: 1px solid #ccc;
  padding: 8px 0 12px;
  font-size: 16px;
  font-weight: bold;
  @include media_desktop {
    border-top: none;
    border-bottom: 1px solid #ccc;
    margin: 10px 16px 48px;
    padding: 8px;
    font-size: 32px;
    font-weight: bold;
  }
}


/*




ex [ ](http://demo3.ec-cube.net/help/agreement)

Markup:
.ec-heading 1 ()

Styleguide 1.1.3
*/

.ec-heading{
  margin: 24px 0;
}



/*
()



ex [ ](http://demo3.ec-cube.net/help/privacy)

Markup:
.ec-heading-bold 

Styleguide 1.1.4
*/

.ec-heading-bold {
  margin: 16px 0;
  font-size: 16px;
  font-weight: bold;
  @include media_desktop {
    font-size: 18px;
  }
}

/*




ex [](http://demo3.ec-cube.net/mypage/history/1063)

Markup:
.ec-rectHeading
  h2 
.ec-rectHeading
  h2 

Styleguide 1.1.5
*/
.ec-rectHeading{
  h1, h2, h3,
  h4, h5, h6{
    background: $clrGray;
    padding: 8px 12px;
    font-size: 20px;
    font-weight: bold;
  }

}


/*




ex [ ](http://demo3.ec-cube.net/shopping/)

Markup:
.ec-reportHeading
  h2 

Styleguide 1.1.6
*/
.ec-reportHeading{
  width: 100%;
  border-top: 1px dotted #ccc;
  margin: 20px 0 30px;
  padding: 0;
  text-align: center;
  font-size: 24px;
  font-weight: bold;
  @include media_desktop {
    border-top: 0;
    font-size: 32px;
  }
  h1, h2, h3,
  h4, h5, h6,p {
    font-weight: bold;
    font-size: 24px;
    @include media_desktop {
      font-size: 32px;
    }
  }
}
